*{
  margin: 0;
  padding: 0;
}
.main-container{
  padding: 20px;
}
.flex{
  display: flex;
}
.nav-li{
  list-style: none;
  padding: 10px;
  font-family: 'Martel', serif;
  font-weight: bold;
  font-size: 20px;
  cursor: pointer;
  color: #4d5f5f;
  font-family: 'Bree Serif', serif;
}
.nav-li:hover{
  color: rgb(68, 153, 179);
}
.nav-li:active{
  border-bottom: rgb(68, 153, 179);
}
.nav-booking{
  background-color: #3333a8;
  color: white;
  padding: 10px;
  border-radius: 20px;
  transition: all;
  transition-duration: .7s;
}
.nav-booking:hover{
  background-color: #a1a1f3;
  color: black;
}
.top-title{
  color: rgb(68, 153, 179);
  font-family: 'Lobster', cursive;
}
.center-both{
  display: flex;
  justify-content: center;
  align-items: center;
}
.justify-between{
 justify-content: space-between;
}
.home-container{
  padding: 10px;
  margin-top: 15px;
  position: relative;

 min-height: 600px;
  background-image: url('nature-background.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.home-h1{
  font-size: 15vw;
    color: #c7c749;
    /* font-family: 'Satisfy', cursive;
    font-family: 'Fugaz One', cursive; */
    font-family: 'Lobster', cursive;
  }
.top-book-now{
  padding: 15px;
  font-size: 5vw;
  border-radius: 25px;
  background-color: #3333a8;
  color: white;
  border: 0px;
  margin: 20px;
  font-family: 'Dancing Script', cursive;
  /* font-family: 'Dancing Script', cursive; */
  position: relative;
cursor: pointer;
z-index: 1000;
pointer-events: auto;
  margin-bottom: 30px;
}

.hamburger-line{
  width: 20px;
  height: 2px;
  margin: 3px;
  background-color: black;
}
.hamburger-icon{
  border:2px solid #4d5f5f;
  height: 25px;
  padding: 4px;
  display: none;
}
.card-places{
  width:50%;
  margin: auto;
  margin-top: 20px;
  margin-bottom: 20px;
}
.form{
  display: flex;
    justify-content: center;
   
}
.book-form{
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.plane-img{
  width: 60%;
  margin: auto;
}
.destinations{
  text-align: center;
  margin: 10px;
  font-family: 'Lobster', cursive;
color: rgb(68, 153, 179);
font-size: 40px;
}
/* Media Query */
@media only screen and (max-width: 760px) {
.nav-menu-container{
  display: none;
    position: absolute;
    right: 20px;
    top: 79px;
    border-radius: 25px;
    width:50%;
    margin: auto;
    background-color: aliceblue;
    padding: 10px;
  z-index: 5;
}
.hamburger-icon{
  display: block;
  cursor: pointer;
}
.home-h1{
  font-size: 18vw;
}
.card-places{
width: 80%;
}
}
  /* Media Query end*/